        <div class="content__frame" @click="show(index)">
          <img :src="portfolio.thumbnail" class="w-100" />
          <div class="content__frame__title">{{ portfolio.title }}</div>
        </div>
        <!-- text -->
        <!-- modal -->
        <div
          :id="index"
          @click="hide(index)"
          :style="ModalActive"
          class="BlackScreen"
        >
          <ModalAku
            v-bind:Background="portfolio.porto"
            :Judul="portfolio.title"
            class=""
          ></ModalAku>
        </div>
      </div>
    </div>
  </div>
</template>
