// import 'swiper/dist/css/swiper.css'
// import { swiper, swiperSlide } from 'vue-awesome-swiper'
import uids1 from "../assets/img/all/uid2.jpg";
// import uid2 from "../assets/img/thumb/dsign2.png";
// import uid3 from "../assets/img/thumb/dsign3.png";
// import uid4 from "../assets/img/thumb/dsign4.png";
import thumb1 from "../assets/img/thumb/porto (1).png";
