<style lang="scss" scoped>
@import "../assets/css/_variable.scss";
@import "../assets/css/_global.scss";
@import "../assets/css/_grid.scss";
.content {
  width: 100%;
  &__frame {
    // background-color: $color-gray;
    background-position: top;
    background-size: cover;
    // height: 250px;
    height: auto;
    margin: 10px;
    border-radius: 4px;
    overflow: hidden;
    position: relative;
    cursor: pointer;

    img {
      transition-duration: 0.6s;
    }
    &__title {
      color: $color-white;
      position: absolute;
      bottom: 14%;
      left: 7%;
      font-size: 4rem;
      transform: translateY(175%);
      transition-duration: 0.6s;
    }

    &:hover {
      img {
        transform: scale(1.2);
        transition-duration: 0.6s;
        filter: brightness(50%);
        -webkit-filter: brightness(50%);
      }
      .content__frame__title {
        transform: translateY(0);
        transition-duration: 0.6s;
      }
    }
  }
}
// modal

.BlackScreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: scroll;
  background-color: rgba($color-black, 0.8);
  z-index: 9999;
}
.Modal {
  &__body {
    img {
      width: 39%;
    }
  }
}
</style>
